<!--
 * @Description: 分销员分组
 * @Author: your name
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div>
    <!-- 筛选条件 -->
    <el-card class="filter-container"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right"
                   @click="searchList"
                   type="primary"
                   size="small">
          查询
        </el-button>
        <el-button style="float: right;margin-right: 15px"
                   @click="reset"
                   size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 40px">
        <el-form :inline="true"
                 :model="list"
                 size="small"
                 label-width="140px">
          <el-form-item label="分组名称：">
            <el-input v-model="list.groupName"
                      clearable
                      placeholder="分组名称"></el-input>
          </el-form-item>
          <el-form-item label="创建时间：">
            <el-date-picker v-model="list.registerTime"
                            type="datetimerange"
                            value-format="yyyy-MM-dd HH-mm-ss"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="operate-container"
             shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
      <span style="margin-left:20px;color: #f59a23;font-weight: 500;font-size:20px">分组总数：{{total}}</span>
      <el-button class="btn-add-export"
                 type="primary"
                 @click="dialogFormVisibleAddGroup = true"
                 size="mini">
        新建分组
      </el-button>
    </el-card>
    <el-card class="operate-container"
             shadow="never">
      <el-table :data="tableList"
                style="width: 100%"
                v-loading="loading"
                border>
        <el-table-column type="index"
                         width="50">
        </el-table-column>
        <el-table-column label="分组名称"
                         width="300"
                         align="center">
          <!-- <template slot-scope="scope">{{scope.row.name}}</template> -->
          <template slot-scope="scope">
            <div v-if="scope.row.edit == false||scope.row.edit == undefined">
              <span>{{scope.row.name}}</span>
              <el-button type="primary"
                         v-if="scope.row.isDefault != 1"
                         plain
                         @click="editName(scope.row)"
                         style="border:none;"
                         icon="el-icon-edit"></el-button>
            </div>
            <div v-if="scope.row.edit == true">
              <el-input style="width:150px"
                        maxlength="7"
                        show-word-limit
                        v-model.trim="scope.row.name"></el-input>
              <el-button type="text"
                         style="color:#909399;"
                         @click="cancleEdit(scope.row)">取消</el-button>
              <el-button type="text"
                         @click="changeName(scope.row)">保存</el-button>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="组成员数"
                         prop="groupUserCount"
                         align="center">
        </el-table-column>
        <el-table-column label="创建时间"
                         prop="createTime"
                         align="center">
        </el-table-column>
        <el-table-column label="操作"
                         align="center">
          <template slot-scope="scope">
            <el-button type="text"
                       class="button_mian"
                       @click="showDetails(scope.row)">查看分组成员</el-button>
            <el-button type="text"
                       class="button_mian"
                       @click="editDetails(scope.row)">编辑</el-button>
            <el-button type="text"
                       class="button_mian"
                       v-if="scope.row.isDefault != 1"
                       @click="auditTrue(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-end"
           style="padding:20px">
        <!-- 页码 -->
        <el-pagination background
                       style="margin-top: 10px;margin-left:20px"
                       @size-change="handleSizeChange"
                       @current-change="pageChange"
                       layout="total,sizes,prev,pager,next,jumper"
                       :page-size="page_size"
                       :page-sizes="[10, 30, 50]"
                       :current-page="pageNum"
                       :total="total">
        </el-pagination>
      </div>
    </el-card>

    <el-dialog title="删除提醒"
               :visible.sync="dialogFormVisible">
      <div style="margin:10px 30px;font-weight:600;font-size:18px">您确定要删除这个分组？</div>
      <div style="margin:10px 30px;font-size:18px">删除以后，该分组下的分销员会自动变回【默认分组】状态。</div>
      <div slot="footer"
           class="flew-row-end">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary"
                   @click="delectGroup()">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="新增分销分组"
               :visible.sync="dialogFormVisibleAddGroup">
      <el-form :inline="true"
               :model="addForm"
               size="small"
               label-width="140px">
        <el-form-item label="分组名称："
                      :rules="[{required:true}]">
          <el-input v-model="addForm.name"
                    clearable
                    maxlength="7"
                    show-word-limit
                    placeholder="分组名称"></el-input>
        </el-form-item>
        <el-form-item label="选择分组成员：">
          <el-select v-model="addForm.groupList"
                     @change="selectGroup"
                     clearable
                     multiple
                     collapse-tags
                     value-key="id"
                     placeholder="请选择分组成员">
            <el-option v-for="item in distributorList"
                       :key="item.id"
                       :label="item.channelUsernickName"
                       :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-table :data="addGroupList"
                style="width: 100%;"
                ref="multipleTable"
                @selection-change="handleSelectionChange"
                border>
        <el-table-column type="selection"
                         width="55">
        </el-table-column>
        <el-table-column label="分销员昵称"
                         prop="channelUsernickName"
                         align="center">
        </el-table-column>
        <el-table-column label="分销员手机号"
                         width="120"
                         prop="channelUserPhone"
                         align="center">
        </el-table-column>
        <el-table-column label="邀请方昵称"
                         width="160"
                         prop="inviternickName"
                         align="center">
          <template slot-scope="scope">
            {{ scope.row.inviternickName == '' ? '--' : scope.row.inviternickName }}
          </template>
        </el-table-column>
        <el-table-column label="邀请方手机号"
                         width="130"
                         prop="inviterPhone"
                         align="center">
          <template slot-scope="scope">
            {{ scope.row.inviterPhone == '' ? '--' : scope.row.inviterPhone }}
          </template>
        </el-table-column>
        <el-table-column label="操作"
                         width="300"
                         align="center">
          <template slot-scope="scope">
            <el-button type="text"
                       @click="delect(scope.$index,scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div slot="footer"
           class="flex-row-between">
        <el-button @click="delectMore">批量删除</el-button>
        <div>
          <el-button @click="dialogFormVisibleAddGroup = false">取 消</el-button>
          <el-button type="primary"
                     @click="addGroup">确 定</el-button>
        </div>

      </div>
    </el-dialog>
  </div>
</template>
<script>
import {
  findChannelGroupList,
  checkGroupName,
  editChannelGroup,
  deleteChannelGroup,
  initChannelUserList,   //查看店铺下的分销员
} from '@/api/distributionManagement/distributor'
export default {
  props: ["status", "user", "shop"],
  data() {
    return {
      dialogFormVisible: false,
      dialogFormVisibleAddGroup: false,
      list: {
        groupName: '',
        registerTime: '',
      },
      form: {
        name: '',
      },
      dialogFormVisibleTwo: false,
      dialogFormVisible: false,
      dialogFormVisibleAddGroup: false,
      id: '',
      tableList: [],
      distributorList: [],   //店铺下分销员
      multipleSelection: [],   //多选框选择
      pageNum: 1,
      page_size: 10,
      total: 0,
      loading: false,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime() - 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },

      levelList: [{
        value: '',
        label: '全部'
      }, {
        value: '1',
        label: '无'
      }, {
        value: '2',
        label: '有'
      }],

      addForm: {
        name: '',
        groupList: []
      },
      addGroupList: [],
    }
  },
  created() {
    this.searchList()
    this.getShopDistributor()
  },
  mounted() {

  },
  methods: {

    //查询店铺下的分销员
    getShopDistributor() {
      initChannelUserList({
        shopId: this.shop.shopId
      }).then(res => {
        this.distributorList = res.data
      }).catch(err => {
        console.log(err)
      })
    },
    //查询数据列表
    searchList() {
      this.loading = true
      findChannelGroupList({
        shopId: this.shop.shopId,
        pageNum: this.pageNum,
        pageSize: this.page_size,
        groupName: this.list.groupName,
        startCreateTime: this.list.registerTime == null ? '' : this.list.registerTime[0],
        endCreateTime: this.list.registerTime == null ? '' : this.list.registerTime[1],
      }).then(res => {
        if (res.code == 200) {
          this.tableList = res.data.list
          this.total = res.data.total
          this.loading = false
          console.log(this.tableList, '=====')
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          });
        }
      })
    },

    // 编辑分组名称
    editName(e) {
      this.$set(e, 'edit', true)
    },
    // 取消编辑分组名称
    cancleEdit(e) {
      this.$set(e, 'edit', false)
      this.searchList()
    },
    //保存分组名称
    changeName(row) {
      if (row.name === '') {
        this.$notify.error({
          title: '失败',
          message: '分组名称不能为空'
        })
        return
      }
      checkGroupName({
        shopId: this.shop.shopId,
        id: row.id,
        name: row.name
      }).then(res => {
        if (res.data == true) {
          editChannelGroup({
            shopId: this.shop.shopId,
            id: row.id,
            name: row.name
          }).then(res => {
            if (res.data == true) {
              this.$notify({
                title: '成功',
                type: 'success',
                message: '修改名称成功'
              })
              this.searchList()
              this.dialogFormVisibleAddGroup = false
            } else {
              this.$notify.error({
                title: '失败',
                message: res.message
              })
            }
          })
        } else {
          this.$notify.error({
            title: "失败",
            message: res.message
          })
        }
      })
    },

    selectGroup(value) {
      this.addForm.groupList = value
      let namesSet = new Set([...this.addGroupList, ...value])
      this.addGroupList = Array.from(namesSet)
    },

    delect(index, row) {
      this.addGroupList.splice(index, 1)
      this.addForm.groupList = this.addGroupList
    },

    //新增分组
    addGroup() {
      if (this.addForm.name === '') {
        this.$notify.error({
          title: '失败',
          message: '分组名称不能为空'
        })
        return
      }
      // if(this.addGroupList.length === 0){
      //   this.$notify.error({
      //     title: '失败',
      //     message: '请选择分销员'
      //   })
      //   return
      // }
      checkGroupName({
        shopId: this.shop.shopId,
        name: this.addForm.name
      }).then(res => {
        if (res.data == true) {
          editChannelGroup({
            shopId: this.shop.shopId,
            name: this.addForm.name,
            channelUserList: this.addGroupList
          }).then(res => {
            if (res.data == true) {
              this.$notify({
                title: '成功',
                type: 'success',
                message: '新增分组成功'
              })
              this.searchList()
              this.dialogFormVisibleAddGroup = false
            } else {
              this.$notify.error({
                title: "失败",
                message: res.message
              })
            }
          })
        } else {
          this.$notify.error({
            title: "失败",
            message: res.message
          })
        }
      })
    },


    //打开删除弹窗
    auditTrue(row) {
      this.dialogFormVisible = true
      this.id = row.id
    },
    //删除分组操作
    delectGroup() {
      deleteChannelGroup({
        shopId: this.shop.shopId,
        id: this.id
      }).then(res => {
        if (res.data == true) {
          this.$notify({
            title: '成功',
            type: 'success',
            message: '删除成功'
          })
          this.searchList()
          this.dialogFormVisible = false
        }
      })
    },

    //查看分组成员
    showDetails(row) {
      this.$router.push({
        path: '/distributionManagement/distributor/groupMember',
        query: {
          id: row.id,
          name: row.name
        }
      })
    },
    editDetails(row) {
      this.$router.push({
        path: '/distributionManagement/distributor/editGroupMember',
        query: {
          id: row.id,
          name: row.name
        }
      })
    },

    handleSelectionChange(value) {
      this.multipleSelection = value;
      console.log(this.multipleSelection, '多选')
    },

    //批量删除
    delectMore() {
      if (this.multipleSelection == null || this.multipleSelection.length == 0) {
        this.$message({
          message: '请选择要操作的申请',
          type: 'warning',
          duration: 1000
        });
        return;
      }
      // this.multipleSelection.forEach(item => {
      //   var index = this.addGroupList.indexOf(item)
      //   console.log(index)
      //   this.addGroupList.splice(index, 1)
      // })
      let arr = this.addGroupList.filter(item => {
        return this.multipleSelection.every(item2 => {
          return item.id != item2.id;
        })
      })
      this.addGroupList = arr
    },
    handleClose() {
      this.dialogFormVisibleTwo = false
      this.form.name = ''
      this.form.orderID = undefined
      this.orderNo = ''
      this.form.month = ''
      this.form.buyTime = ''
    },
    //重置
    reset() {
      this.list = {
        userName: '',
        cellPhone: '',
        level: '',
        parentCellPhone: '',
        parentUserName: '',
        registerTime: '',
      }
      this.searchList()
    },
    //修改每页页数
    handleSizeChange(page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange(page) {
      this.pageNum = page
      this.searchList()
    },
  },
  computed: {
    startTime() {
      return this.status;
    },
  },
  watch: {
    startTime(newValue, oldValue) {
      this.searchList()
      return newValue;
    },
  }
}
</script>

<style lang="scss" scoped>
.dialog-footer {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.button_mian {
  margin: 5px;
}
.btn-add-export {
  float: right;
  margin-top: -8px;
}
.operate-container {
  margin-top: 10px;
}
.btn-add {
  float: right;
  margin-top: -8px;
}
.store-red {
  margin-left: 70px;
  color: red;
  span {
    display: block;
    margin-bottom: 10px;
  }
}
.table-container {
  margin-top: 10px;
}
</style>
